const pako = require('pako');

const ANTI_CHEAT_CODE = 'Fe12NAfA3R6z4k0z';
const ZLIB_HEADER = '7a990d405d2c6fb93aa8fbb0ec1a3b23';

const decode = encoded => {
    let output = '';
    if (encoded.substr(0, 32) === ZLIB_HEADER) {
        output = decoder_zlib(encoded);
    } else {
        output = decoder_base64(encoded);
    }
    return output;
};

const decoder_base64 = encoded => {
    let output = '';
    if (encoded.indexOf(ANTI_CHEAT_CODE) > 0) {
        const _local1 = encoded.split(ANTI_CHEAT_CODE)[0];
        const _local1Length = _local1.length;
        let temp = '';
        for (let i = 0; i < _local1Length; i += 2)
            temp += _local1[i];
        output = JSON.parse(atob(temp));
    } else {
        output = JSON.parse(encoded);
    }
    return output;
};

const decoder_zlib = encoded => {
    let _local1 = atob(encoded.substr(32));
    let _local2 = pako.inflate(_local1, {to: 'string'});
    return JSON.parse(_local2);
};

export default decode;