export const largeNumberFormat = number => {
    if (number.lt(0)) {
        return '-' + largeNumberFormat(number.negated());
    }
    if (number.lt(10000)) {
        return number.toFixed(0);
    } else {
        return number.toExponential(4).replace('+', '');
    }
};

export const smallNumberFormat = number => {
    return number.toLocaleString();
};

export const timeDurationFormat = timeDuration => {
    let timeSecond = timeDuration / 1000;
    const _day = Math.floor(timeSecond / 86400);
    timeSecond -= _day * 86400;
    const _hour = Math.floor(timeSecond / 3600);
    timeSecond -= _hour * 3600;
    const _minute = Math.floor(timeSecond / 60);
    return (_day === 0 ? '' : _day + 'd ') + (_hour === 0 ? '' : _hour + 'h ') + (_minute + 'm');
};
