import React from 'react';
import decode from '../../utilities/decoder';
import style from './style.module.scss';

export default class ListerInput extends React.Component {
    constructor(props) {
        super(props);
        this.fileInputRef = React.createRef();
    }

    _handleClick = () => {
        this.fileInputRef.current.click();
    };

    _onChangeFileInput = e => {
        const {onChange} = this.props;
        let fr = new FileReader();
        fr.readAsText(e.target.files[0]);
        fr.onload = () => {
            try {
                const _local1 = fr.result.trim();
                if (_local1.length > 0) {
                    let x = decode(fr.result);
                    onChange && onChange(x);
                }
            } catch (e) {
                console.log(e);
                window.alert('Error: Save corrupted and cannot be read.');
            }
        };
    };

    _onChangeTextInput = e => {
        try {
            const {onChange} = this.props;
            const _local1 = e.target.value.trim();
            if (_local1.length > 0) {
                let x = decode(_local1);
                onChange && onChange(x);
            }
        } catch (e) {
            console.log(e);
            window.alert('Error: Save corrupted and cannot be read.');
        }
    };

    render = () => {
        // const {onChange} = this.props;
        return (
            <div className={style.inputWrapper}>
                <input ref={this.fileInputRef} type={'file'} style={{display: 'none'}}
                       onChange={this._onChangeFileInput}/>
                <button className={style.inputFile} onClick={this._handleClick}>Load from file...</button>
                <input className={style.inputText} type={'text'} placeholder={'...or paste your save here'}
                       onChange={this._onChangeTextInput}/>
            </div>
        )
    };
}