import React from 'react';
import style from './style.module.scss';
import _ from 'lodash';
import {Decimal} from 'decimal.js';
import {largeNumberFormat, timeDurationFormat} from '../../utilities/formatter';
import TextareaShowCurrentLength from '../TextareaShowCurrentLength';

export default class ListerTranscension extends React.Component {
    _buildTranscensionLog = stats => {
        let x = [];
        Object.values(stats).forEach(stat => {
            let _local = '';
            _local += '|' + _.padStart(stat.id.toString(), 5) + '|';
            _local += _.padStart(timeDurationFormat(stat.endTime - stat.startTime), 12) + '|';
            _local += _.padStart(stat.numAscensions.toString(), 10) + '|';
            _local += _.padStart(stat.highestZoneEver.toString(), 7) + '|';
            _local += _.padStart(largeNumberFormat(Decimal(stat.heroSoulsGained)), 13) + '|';
            if (Decimal(stat.heroSoulsGained).equals(0)) {
                _local += _.padStart('0', 8) + '|';
            } else {
                _local += _.padStart(largeNumberFormat(Decimal.log10(stat.heroSoulsGained).times(5).floor()), 8) + '|';
            }
            x.push(_local);
        });
        return _.isEmpty(x) ? '' : 'Transcension Log:\n\n| No. |  Duration  |Ascensions|  HZE  |  HS gained  |   AS   |\n|:---:|:----------:|:--------:|:-----:|:-----------:|:------:|\n' + x.join('\n');
    };

    render = () => {
        const {stat} = this.props;
        const tlog = this._buildTranscensionLog(stat);
        return (
            <div className={style.wrapper}>
                <TextareaShowCurrentLength value={tlog} placeholder={'Transcension Log'}/>
            </div>
        )
    }
}