import React from 'react';
import style from './style.module.scss';

export default class TextareaShowCurrentLength extends React.PureComponent {
    render = () => {
        const {value, placeholder} = this.props;
        const length = value.length;
        return (
            <div className={style.textareaWrapper}>
                <textarea className={style.textarea} value={value} placeholder={placeholder} readOnly onClick={e => e.target.select()}/>
                <span className={style.count}>{length}</span>
            </div>
        )
    }
}