import React from 'react';
import style from './style.module.scss';
import {faCheck, faTimes} from '@fortawesome/free-solid-svg-icons';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

class ListerToggleButton extends React.PureComponent {
    render() {
        const {id, name, show, onClick} = this.props;
        return (
            <button className={show ? style.buttonShow : style.buttonHide}
                    onClick={() => onClick(id)}>
                <FontAwesomeIcon icon={show ? faCheck : faTimes} style={{marginRight: 2}}/>
                {name}
            </button>
        )
    }
}

export default class ListerToggleDisplay extends React.Component {
    render() {
        const {display, onClick} = this.props;
        return (
            <div className={style.wrapper}>
                <span>Display:</span>
                {
                    Object.keys(display).map(i => {
                        const d = display[i];
                        return <ListerToggleButton key={d.id} id={d.id} name={d.name}
                                                   show={d.show} onClick={onClick}/>
                    })
                }
            </div>
        )
    }
}