import React from 'react';
import style from './style.module.scss';
import * as Constants from '../../Constants';
import _ from 'lodash';
import {Decimal} from 'decimal.js';
import {largeNumberFormat, smallNumberFormat, timeDurationFormat} from '../../utilities/formatter';
import TextareaShowCurrentLength from '../TextareaShowCurrentLength';

export default class ListerDisplay extends React.Component {
    _showInfo = (input, display) => {
        let outsiders = '';
        if (!_.isUndefined(input.outsiders) && display.outsiders.show) {
            outsiders = this._showOutsiders(input.outsiders.outsiders);
        }

        let ancients = '';
        if (!_.isUndefined(input.ancients) && display.ancients.show) {
            ancients = this._showAncients(input.ancients.ancients);
        }

        let gilds = '';
        if (!_.isUndefined(input.heroCollection) && display.gilds.show) {
            gilds = this._showGilds(input.heroCollection.heroes);
        }

        let miscs = '';
        if (!_.isEmpty(input) && display.miscs.show) {
            miscs = this._showMiscs(input);
        }

        let times = '';
        if (!_.isEmpty(input) && display.times.show) {
            times = this._showTimes(input);
        }

        let relics = '';
        if (!_.isUndefined(input.items) && display.relics.show) {
            relics = this._showRelics(input.items);
        }

        let events = '';
        if (!_.isEmpty(input) && display.events.show) {
            events = this._showEvents(input);
        }

        let totalRelics = '';
        if (!_.isUndefined(input.items) && display.totalrelics.show) {
            totalRelics = this._showTotalRelics(input.items);
        }

        return [outsiders, ancients, gilds, miscs, times, relics, events, totalRelics].filter((i) => {
            return i !== '';
        }).join('\n\n');
    };

    _showOutsiders = outsiders => {
        let x = [];
        Constants.outsiders.forEach(item => {
            if (outsiders[item.id]) {
                x.push(item.name + ' (' + outsiders[item.id].level + ')');
            }
        });
        return '**Outsiders:** ' + x.join(', ') + '.';
    };

    _showAncients = ancients => {
        let x = [];
        let y = [];
        Constants.ancients.forEach(item => {
            if (ancients[item.id]) {
                if (Decimal(ancients[item.id].level).equals(0)) {
                    y.push(item);
                } else {
                    item.level = Decimal(ancients[item.id].level);
                    x.push(item);
                }
            }
        });
        x = x.sort((a, b) => {
            let comparison = 0;
            if (a.level.greaterThan(b.level)) {
                comparison = -1;
            } else if (a.level.lessThan(b.level)) {
                comparison = 1;
            } else {
                comparison = a.name.localeCompare(b.name);
            }
            return comparison;
        }).map(item => {
            return item.name + ' (' + largeNumberFormat(Decimal(ancients[item.id].level)) + ')'
        });
        y = y.sort((a, b) => {
            return a.name.localeCompare(b.name);
        }).map(item => {
            return item.name;
        });
        return '**Ancients:** ' + (_.isEmpty(x) ? 'None' : x.join(', ')) + '.\n\n**Not Summoned:** ' + (_.isEmpty(y) ? 'None' : y.join(', ')) + '.';
    };

    _showGilds = heroes => {
        let x = [];
        Constants.heroes.forEach(hero => {
            if (heroes[hero.id] && heroes[hero.id].epicLevel > 0) {
                x.push(hero.name + ' (' + heroes[hero.id].epicLevel.toLocaleString() + ')');
            }
        });
        return '**Gilds:** ' + (_.isEmpty(x) ? 'None.' : x.join(', ')) + '.';
    };

    _showMiscs = input => {
        let x = [];
        //about AS
        const currentHS = Decimal(input.heroSouls);
        const sacrificedHS = Decimal(input.heroSoulsSacrificed);
        let spentHS = Decimal(input.ancients.rerollSoulsSpent);
        for (let i in input.ancients.ancients) {
            spentHS = spentHS.plus(Decimal(input.ancients.ancients[i].spentHeroSouls));
        }
        let currentASGain = Decimal(0);
        if (sacrificedHS.plus(currentHS).plus(spentHS).gt(0)) {
            currentASGain = Decimal.log10(sacrificedHS.plus(currentHS).plus(spentHS)).times(5).floor().minus(Decimal(input.ancientSoulsTotal));
        }
        x.push('AS (' + smallNumberFormat(input.ancientSoulsTotal) + ' + ' + smallNumberFormat(currentASGain.toNumber()) + ', ' + (input.ancientSouls !== 0 ? ('***Unspent: ' + input.ancientSouls + '***') : ('Unspent: ' + smallNumberFormat(input.ancientSouls))) + ')');

        //about TP
        let tp = 25 - 23 * Math.exp(-0.0003 * Number(input.ancientSoulsTotal));
        x.push('TP (' + Decimal(tp).toFixed(3) + '%)');

        //about HS
        x.push('HS (' + largeNumberFormat(currentHS) + ', Spent: ' + largeNumberFormat(spentHS) + ', Sacrificed: ' + largeNumberFormat(sacrificedHS) + ')');

        //about Zone
        x.push('Highest Zone (This Ascension: ' + smallNumberFormat(input.highestFinishedZone) + ', This Transcension: ' + smallNumberFormat(input.highestFinishedZonePersist) + ', Ever: ' + smallNumberFormat(Math.max(input.transcendentHighestFinishedZone, input.pretranscendentHighestFinishedZone)) + ')');

        //about Transcension and Ascension
        x.push('Transcensions (' + smallNumberFormat(input.numberOfTranscensions) + ')');
        x.push('Ascensions (This Transcension: ' + smallNumberFormat(input.numAscensionsThisTranscension) + ', Total: ' + smallNumberFormat(input.numWorldResets) + ')');

        //about Relics Found
        x.push('Relics Found (This Transcension: ' + smallNumberFormat(input.relicsReceivedThisTranscension) + ', Total: ' + smallNumberFormat(input.totalRelicsReceived) + ')');

        //about ID
        x.push('Immortal Damage (' + largeNumberFormat(Decimal(input.titanDamage)) + ')');

        //about Rubies
        x.push('Rubies (' + smallNumberFormat(input.rubies) + ')');

        //about the Ruby DPS Multiplier
        x.push('2x multiplier to all your dmg (' + (input.paidForRubyMultiplier === true ? 'Yes' : 'No') + ')');

        //about AutoClickers
        x.push('Auto Clickers (' + smallNumberFormat(Number(input.autoclickers) + Number(input.dlcAutoclickers)) + ')');

        // //about Achievements
        x.push('Achievements (' + Object.values(input.achievements).filter(Boolean).length + '/167)');
        return '**Miscs:** ' + x.join('; ') + '.';
    };

    _showTimes = input => {
        let x = [];
        const currentTime = new Date().getTime();
        x.push('Since Start (' + timeDurationFormat(currentTime - input.creationTimestamp) + ')');
        if (input.transcensionTimestamp !== 0) {
            x.push('Since Transcension (' + timeDurationFormat(currentTime - input.transcensionTimestamp) + ')');
        }
        x.push('Since Ascension (' + timeDurationFormat(currentTime - input.startTimestamp) + ')');
        return '**Times:** ' + x.join('; ') + '.';
    };

    _showRelics = items => {
        let x = [];

        for (const i in items.slots) {
            if (i > 4) {
                continue;
            }
            const relic = items.items[items.slots[i]];
            let _bonus = [];
            for (let j = 1; j <= 4; j++) {
                if (relic['bonusType' + j] !== 0) {
                    const _ancient = Constants.ancients.filter(ancient => {
                        return ancient.bonusType === relic['bonusType' + j];
                    })[0];
                    _bonus.push(' +' + Number(relic['bonus' + j + 'Level']).toLocaleString() + ' ' + _ancient.name);
                }
            }
            x.push('* *' + relic.name + '*:' + _bonus.join(',') + ';');
        }
        return '**Relics:**' + (_.isEmpty(x) ? ' None.' : '\n\n' + x.join('\n'));
    };

    _showEvents = input => {
        let x = [];

        //presents
        x.push('Presents (Opened: ' + smallNumberFormat(input.openedClickmasPresents) + '; Unopened: ' + smallNumberFormat(input.unopenedClickmasPresents) + ')');

        //rewards
        x.push('Rewards (Candy Canes: ' + smallNumberFormat(input.candyCanesEarned) + '; Rubies: ' + smallNumberFormat(input.clickmasRubiesEarned) + ')');
        return '**Event:** ' + x.join('; ') + '.';
    };

    _showTotalRelics = items => {
        let x = [];
        Constants.ancients.forEach(ancient => {
            let _level = 0;
            for (const i in items.slots) {
                if (i > 4) {
                    continue;
                }
                const relic = items.items[items.slots[i]];
                for (let j = 1; j <= 4; j++) {
                    if (relic['bonusType' + j] === ancient.bonusType) {
                        _level += Number(relic['bonus' + j + 'Level']);
                    }
                }
            }
            if (_level > 0) {
                x.push('* +' + _level.toLocaleString() + ' ' + ancient.name);
            }
        });
        return '**Total Relic Bonuses:** ' + (_.isEmpty(x) ? 'None' : '\n\n' + x.join('\n'));
    };

    render = () => {
        const {input, display} = this.props;
        const info = this._showInfo(input, display);
        return (
            <div className={style.wrapper}>
                <TextareaShowCurrentLength value={info} placeholder={'Overall Infomation'}/>
            </div>
        )
    }
}