import React from 'react';
import _ from 'lodash';
import style from './style.module.scss';
import InitialStates from '../../InitialStates';
import ListerToggleDisplay from '../../components/ListerToggleDisplay';
import ListerInput from '../../components/ListerInput';
import ListerDisplay from '../../components/ListerDisplay';
import ListerTranscension from '../../components/ListerTranscension';
import ListerAscension from '../../components/ListerAscension';

export default class Lister extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            input: {},
            display: InitialStates.display
        };
    }

    _handleChange = x => {
        this.setState({
            input: x
        });
    };

    _handleToggleDisplay = (id) => {
        let display = this.state.display;
        Object.keys(display).forEach(i => {
            if (display[i].id === id) {
                display[i].show = !display[i].show;
            }
        });
        this.setState({
            display: display
        });
    };

    render = () => {
        const {input, display} = this.state;
        return (
            <div className={style.wrapper}>
                <h1>CLICKER LISTER</h1>
                <ListerInput onChange={this._handleChange}/>
                <ListerToggleDisplay display={display} onClick={this._handleToggleDisplay}/>
                <ListerDisplay input={input} display={display}/>
                {
                    <ListerTranscension
                        stat={(_.isUndefined(input.stats)) ? {} : input.stats.transcensions}/>
                }
                {
                    <ListerAscension stat={(_.isUndefined(input.stats) ? {} : input.stats.transcensions)}/>
                }
            </div>
        )
    }
}