import React from 'react';
import style from './style.module.scss';
import TextareaShowCurrentLength from '../TextareaShowCurrentLength';
import _ from 'lodash';
import {largeNumberFormat, timeDurationFormat} from '../../utilities/formatter';
import {Decimal} from 'decimal.js';

export default class ListerAscension extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            index: -1
        }
    }

    _buildAscensionLog = (stat, index) => {
        if (index === -1) {
            return '';
        } else {
            const ascensions = stat[index].ascensions;
            let x = [];
            Object.values(ascensions).forEach(ascension => {
                let _local = '';
                _local += '|' + _.padStart(ascension.id.toString(), 5) + '|';
                _local += _.padStart(timeDurationFormat(ascension.endTime - ascension.startTime), 12) + '|';
                _local += _.padStart(ascension.highestZoneEver.toString(), 7) + '|';
                _local += _.padStart(largeNumberFormat(Decimal(ascension.heroSoulsEnd).minus(ascension.heroSoulsStart)), 13) + '|';
                x.push(_local);
            });
            return _.isEmpty(x) ? '' : 'Ascensions in Transcension #' + index + ':\n\n| No. |  Duration  |  HZE  |  HS gained  |\n|:---:|:----------:|:-----:|:-----------:|\n' + x.join('\n');
        }
    };

    _handleSelectorChange = (e) => {
        this.setState({
            index: e.target.value
        });
    };

    render = () => {
        const {stat} = this.props;
        const {index} = this.state;
        const alog = this._buildAscensionLog(stat, index);
        return (
            <div className={style.wrapper}>
                <select className={style.selector} value={index} onChange={this._handleSelectorChange}>
                    <option value={-1} disabled>Select transcension to view log</option>
                    {
                        Object.values(stat).map(t => (
                            <option key={`${t.id}`} value={t.id}>Transcension {t.id}</option>
                        ))
                    }
                </select>
                <TextareaShowCurrentLength value={alog} placeholder={'Ascension Log'}/>
            </div>
        )
    }
}