import * as React from 'react';
import {NavLink} from 'react-router-dom';
import style from './style.module.scss';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faPlus} from '@fortawesome/free-solid-svg-icons'

export default class SideBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isSideBarExpanded: false
        }
    }

    _toggleSideBar = () => {
        const {isSideBarExpanded} = this.state;
        this.setState({
            isSideBarExpanded: !isSideBarExpanded
        });
    };

    render() {
        const {isSideBarExpanded} = this.state;
        const {links} = this.props;
        // console.log('RENDER!');
        return (
            <div className={[style.sideBar, (isSideBarExpanded) ? style.sideBarExpanded : null].join(' ')}>
                <button className={style.buttonToggle} onClick={this._toggleSideBar}>
                    <FontAwesomeIcon icon={faPlus} size={'2x'} transform={{rotate: isSideBarExpanded ? 45 : 0}}/>
                </button>
                {
                    isSideBarExpanded
                        ? <div className={style.sideBarButtonGroup}>
                            {links.map((item, index) => (
                                <NavLink key={index} exact to={item.href} className={style.sideBarLink}
                                         activeClassName={style.sideBarLinkActive}>{item.title}</NavLink>
                            ))}
                        </div>
                        : null
                }
            </div>
        )
    }
}